
.add-data { width: 600px; margin: 30px auto 0; padding:20px; background: #fff;
}
.add-data h3 { padding-bottom: 20px;
}
.add-data h3 span {color: #c00;
}
.dialog-content-table { margin: 10px 15px; width: 570px;
}
.el-table .warning-row {
    background: oldlace;
}
.el-table .success-row {
    background: #f0f9eb;
}
.dialog-pagination {
    padding: 5px 15px;
    text-align: right;
}
.dialog {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1000;
}
.dialog-content {
    position: absolute;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, .4);
            box-shadow: 0 0 10px rgba(0, 0, 0, .4);
    width: 600px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #fff;
    z-index: 1010;
    border-radius: 4px;
}
.dialog-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    padding: 0 15px;
}
.dialog-title-close {
    width: 20px;
    height: 20px;
    font-size: 16px;
    cursor: pointer;
    text-align: center;
}
.dialog-content-box {
    max-height: 500px;
    max-height: calc(500px - 88px);
    overflow-y: auto;
    overflow-x: hidden;
}
.dailog-btn {
    padding: 10px 25px;
    text-align: right;
    background: #eee;
    border-top: 1px solid #ccc;
    border-radius: 0 0 4px 4px;
}
.dialog-title {
    height: 40px;
    background: #eee;
    border-bottom: 1px solid #ccc;
    border-radius: 4px 4px 0 0;
}
.dialog-mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .3);
    z-index: 1009;
}



